<template>
  <div class="games-filter-title">All games <span v-if="provider[0]">({{ provider[0] }})</span></div>
  <div class="games-catalog">
    <div class="catalog">
      <div class="catalog-subtitle">
        Total games: {{ searchGames.length }}
      </div>
      <div class="catalog-list">
        <div class="catalog-item" v-for="(item, key) in searchGames" :key="key">
          <div class="game">
            <div class="game-top">
              <div class="game-block-img">
                <img :src="item.imgUrl" :alt="item.name" class="game-img">
              </div>
              <div class="game-action">
                <div class="game-btn">
                  <div class="game-play">
                    <button class="btn">Play</button>
                  </div>
                </div>
              </div>
            </div>
            <div class="game-bottom">
              <div class="game-title">
                <a href="" class="link-no-styles">{{ item.name }}</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import filterProvider from '@/mixins/filterProvider'
export default {
  data () {
    return {
      games: [],
      providersFilter: [],
      catagory: 'all',
      keyword: ''
    }
  },
  mixins: [filterProvider],
  methods: {
    filter () {
      this.games = this.tempGames
    },
    providerFilter () {
      this.providersFilter = [...new Set(this.games.map(item => item.gameProvider))]
    },
    sendData () {
      this.emitter.emit('sendProvidersFilter', this.providersFilter)
      this.emitter.emit('sendCatagory', this.catagory)
    },
    filterRouterPath () {
      const id = this.$route.params.id
      if (id) {
        for (let i = 0; i < this.games.length; i++) {
          if (id === this.games[i].routerPath) {
            this.provider = [this.games[i].gameProvider]
          }
        }
        this.emitter.emit('sendProvider', this.provider[0])
        this.filterProvider()
        this.games = this.filterGames
      }
    }
  },
  computed: {
    searchGames () {
      this.emitter.on('keyword-updated', (item) => {
        this.keyword = item
      })
      if (!this.keyword) {
        return this.games
      }
      return this.games.filter(game => game.name.toLowerCase().includes(this.keyword.toLowerCase()))
    }
  },
  created () {
    this.filter()
    this.providerFilter()
    this.sendData()
    this.filterRouterPath()
  },
  inject: ['tempGames', 'emitter']
}
</script>
