export default {
  data () {
    return {
      filterGames: [],
      provider: []
    }
  },
  methods: {
    filterProvider () {
      this.filterGames = []
      this.games.forEach((item) => {
        if (item.gameProvider === this.provider[0]) {
          this.filterGames.push(item)
        }
      })
    }
  },
  created () {
    this.emitter.on('sendProvider', (item) => {
      if (item === '') {
        this.filter()
        this.provider = []
      } else {
        this.filter()
        this.provider = []
        this.provider.push(item)
        this.filterProvider()
        this.games = this.filterGames
      }
    })
  },
  inject: ['emitter'],
  beforeMount () {
    this.emitter.off('sendProvider', (item) => {
      if (item === '') {
        this.filter()
        this.provider = []
      } else {
        this.filter()
        this.provider = []
        this.provider.push(item)
        this.filterProvider()
        this.games = this.filterGames
      }
    })
  }
}
